export function listRsvp(httpClient: HttpClient): ListRsvpSignature {
  return (options?: ListRsvpOptions) =>
    universalListRsvp(
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface ListRsvpSignature {
  /**
   * Retrieves a list of up to 100 RSVPs.
   * @param - Optional fields.
   * @deprecated
   */
  (options?: ListRsvpOptions): Promise<
    NonNullablePaths<
      ListRsvpResponse,
      | `total`
      | `offset`
      | `limit`
      | `rsvps`
      | `rsvps.${number}._id`
      | `rsvps.${number}.eventId`
      | `rsvps.${number}.contactId`
      | `rsvps.${number}.memberId`
      | `rsvps.${number}.firstName`
      | `rsvps.${number}.lastName`
      | `rsvps.${number}.email`
      | `rsvps.${number}.status`
      | `rsvps.${number}.totalGuests`
      | `rsvps.${number}.anonymized`,
      4
    >
  >;
}

