export const onRsvpCreated = EventDefinition(
  'wix.events.rsvp.events.RsvpCreated',
  false,
  (event: RsvpCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'data.timestamp' },
            { path: 'data.rsvpForm.inputValues.dateTime' },
            { path: 'data.guests.checkIn.created' },
          ],
        },
        {
          transformFn: transformRESTFloatToSDKFloat,
          paths: [{ path: 'data.rsvpForm.inputValues.number' }],
        },
        {
          transformFn: transformRESTAddressToSDKAddress,
          paths: [{ path: 'data.rsvpForm.inputValues.address.address' }],
        },
      ])
    )
)<RsvpCreatedEnvelope>();
export const onRsvpDeleted = EventDefinition(
  'wix.events.rsvp.events.RsvpDeleted',
  false,
  (event: RsvpDeletedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [{ path: 'data.timestamp' }],
        },
      ])
    )
)<RsvpDeletedEnvelope>();
export const onRsvpUpdated = EventDefinition(
  'wix.events.rsvp.events.RsvpUpdated',
  false,
  (event: RsvpUpdatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'data.timestamp' },
            { path: 'data.created' },
            { path: 'data.rsvpForm.inputValues.dateTime' },
            { path: 'data.guests.checkIn.created' },
          ],
        },
        {
          transformFn: transformRESTFloatToSDKFloat,
          paths: [{ path: 'data.rsvpForm.inputValues.number' }],
        },
        {
          transformFn: transformRESTAddressToSDKAddress,
          paths: [{ path: 'data.rsvpForm.inputValues.address.address' }],
        },
      ])
    )
)<RsvpUpdatedEnvelope>();

